'use client'

import { useState, useEffect } from 'react'
import { useRouter } from 'next/navigation'
import { createClient } from '@/lib/supabase/client'
import { Button } from '@/components/ui/button'
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from '@/components/ui/card'
import { Progress } from '@/components/ui/progress'
import { Users, Clock, BookOpen, TrendingUp, AlertCircle, Sparkles } from 'lucide-react'
import Link from 'next/link'

export const dynamic = 'force-dynamic'

export default function TrialTeacherDashboard() {
  const [user, setUser] = useState<any>(null)
  const [profile, setProfile] = useState<any>(null)
  const [trialStudents, setTrialStudents] = useState<any[]>([])
  const [loading, setLoading] = useState(true)
  const [daysLeft, setDaysLeft] = useState(0)
  const router = useRouter()

  useEffect(() => {
    loadTrialData()
  }, [])

  async function loadTrialData() {
    const supabase = createClient()

    try {
      const {
        data: { user: authUser },
        error: userError,
      } = await supabase.auth.getUser()

      if (userError || !authUser) {
        router.push('/auth/login')
        return
      }

      setUser(authUser)

      // Load profile
      const { data: profileData } = await supabase
        .from('profiles')
        .select('*')
        .eq('id', authUser.id)
        .single()

      if (profileData?.teacher_mode !== 'trial') {
        router.push('/protected/teacher')
        return
      }

      setProfile(profileData)

      // Calculate days left in trial
      if (profileData.trial_end_date) {
        const endDate = new Date(profileData.trial_end_date)
        const now = new Date()
        const diff = Math.ceil((endDate.getTime() - now.getTime()) / (1000 * 60 * 60 * 24))
        setDaysLeft(Math.max(0, diff))
      }

      // Load trial students (mock data for now)
      setTrialStudents([])
    } catch (err: any) {
      console.error('[v0] Error loading trial data:', err)
    } finally {
      setLoading(false)
    }
  }

  const studentLimit = profile?.trial_student_count || 3
  const studentProgress = (trialStudents.length / studentLimit) * 100

  if (loading) {
    return (
      <div className="flex items-center justify-center min-h-screen bg-black">
        <p className="text-white/60">Loading trial dashboard...</p>
      </div>
    )
  }

  return (
    <div className="min-h-screen bg-black text-white">
      <header className="border-b border-white/10 bg-black/80 backdrop-blur-xl">
        <div className="max-w-7xl mx-auto px-4 py-4">
          <div className="flex items-center justify-between">
            <div>
              <h1 className="text-2xl font-bold text-white">Trial Mode Dashboard</h1>
              <p className="text-sm text-white/60">Test Averon CodeLab with up to {studentLimit} students</p>
            </div>
            <Link href="/protected">
              <Button variant="ghost" className="text-white/60 hover:text-white hover:bg-white/5">
                Back
              </Button>
            </Link>
          </div>
        </div>
      </header>

      <main className="max-w-7xl mx-auto px-4 py-8 space-y-6">
        {/* Trial Status Banner */}
        <Card className="border-cyan-500/30 bg-gradient-to-br from-cyan-500/10 to-blue-500/10 backdrop-blur-sm">
          <CardHeader>
            <div className="flex items-center justify-between">
              <div className="flex items-center gap-3">
                <div className="w-12 h-12 rounded-full bg-cyan-500/20 flex items-center justify-center">
                  <Sparkles className="w-6 h-6 text-cyan-400" />
                </div>
                <div>
                  <CardTitle className="text-white">Free Trial Active</CardTitle>
                  <CardDescription className="text-white/60">
                    {daysLeft} days remaining
                  </CardDescription>
                </div>
              </div>
              <Link href="/pricing">
                <Button className="bg-gradient-to-r from-cyan-500 to-blue-500 hover:from-cyan-400 hover:to-blue-400 text-white font-semibold">
                  Upgrade Now
                </Button>
              </Link>
            </div>
          </CardHeader>
          <CardContent>
            <div className="space-y-2">
              <div className="flex items-center justify-between text-sm">
                <span className="text-white/60">Students</span>
                <span className="text-white font-semibold">{trialStudents.length} / {studentLimit}</span>
              </div>
              <Progress value={studentProgress} className="h-2" />
            </div>
          </CardContent>
        </Card>

        {/* Quick Stats */}
        <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
          <Card className="border-white/10 bg-white/5 backdrop-blur-sm">
            <CardHeader>
              <div className="flex items-center justify-between">
                <Users className="w-5 h-5 text-cyan-400" />
                <span className="text-3xl font-bold text-white">{trialStudents.length}</span>
              </div>
            </CardHeader>
            <CardContent>
              <p className="text-sm text-white/60">Active Students</p>
            </CardContent>
          </Card>

          <Card className="border-white/10 bg-white/5 backdrop-blur-sm">
            <CardHeader>
              <div className="flex items-center justify-between">
                <BookOpen className="w-5 h-5 text-blue-400" />
                <span className="text-3xl font-bold text-white">0</span>
              </div>
            </CardHeader>
            <CardContent>
              <p className="text-sm text-white/60">Lessons Assigned</p>
            </CardContent>
          </Card>

          <Card className="border-white/10 bg-white/5 backdrop-blur-sm">
            <CardHeader>
              <div className="flex items-center justify-between">
                <TrendingUp className="w-5 h-5 text-green-400" />
                <span className="text-3xl font-bold text-white">0%</span>
              </div>
            </CardHeader>
            <CardContent>
              <p className="text-sm text-white/60">Avg Progress</p>
            </CardContent>
          </Card>
        </div>

        {/* Getting Started */}
        <Card className="border-white/10 bg-white/5 backdrop-blur-sm">
          <CardHeader>
            <CardTitle className="text-white">Getting Started with Trial Mode</CardTitle>
            <CardDescription className="text-white/60">
              Follow these steps to try out Averon CodeLab
            </CardDescription>
          </CardHeader>
          <CardContent>
            <div className="space-y-4">
              <div className="flex items-start gap-4 p-4 rounded-lg bg-white/5 border border-white/10">
                <div className="w-8 h-8 rounded-full bg-cyan-500/20 flex items-center justify-center flex-shrink-0">
                  <span className="text-cyan-400 font-bold">1</span>
                </div>
                <div>
                  <h4 className="font-semibold text-white mb-1">Invite Students</h4>
                  <p className="text-sm text-white/60">
                    Share your class code with up to {studentLimit} students to join your trial classroom
                  </p>
                </div>
              </div>

              <div className="flex items-start gap-4 p-4 rounded-lg bg-white/5 border border-white/10">
                <div className="w-8 h-8 rounded-full bg-cyan-500/20 flex items-center justify-center flex-shrink-0">
                  <span className="text-cyan-400 font-bold">2</span>
                </div>
                <div>
                  <h4 className="font-semibold text-white mb-1">Explore Python Basics</h4>
                  <p className="text-sm text-white/60">
                    Access the full Python Fundamentals course during your trial period
                  </p>
                </div>
              </div>

              <div className="flex items-start gap-4 p-4 rounded-lg bg-white/5 border border-white/10">
                <div className="w-8 h-8 rounded-full bg-cyan-500/20 flex items-center justify-center flex-shrink-0">
                  <span className="text-cyan-400 font-bold">3</span>
                </div>
                <div>
                  <h4 className="font-semibold text-white mb-1">Track Progress</h4>
                  <p className="text-sm text-white/60">
                    Monitor student engagement and learning with real-time analytics
                  </p>
                </div>
              </div>
            </div>
          </CardContent>
        </Card>

        {/* Trial Limitations Notice */}
        <Card className="border-orange-500/30 bg-orange-500/10 backdrop-blur-sm">
          <CardHeader>
            <div className="flex items-center gap-3">
              <AlertCircle className="w-5 h-5 text-orange-400" />
              <CardTitle className="text-white">Trial Limitations</CardTitle>
            </div>
          </CardHeader>
          <CardContent>
            <ul className="space-y-2 text-sm text-white/80">
              <li className="flex items-start gap-2">
                <span className="text-orange-400">•</span>
                <span>Limited to {studentLimit} students maximum</span>
              </li>
              <li className="flex items-start gap-2">
                <span className="text-orange-400">•</span>
                <span>Access to Python Fundamentals course only</span>
              </li>
              <li className="flex items-start gap-2">
                <span className="text-orange-400">•</span>
                <span>{daysLeft} days remaining in trial period</span>
              </li>
              <li className="flex items-start gap-2">
                <span className="text-orange-400">•</span>
                <span>Upgrade anytime to unlock all features and courses</span>
              </li>
            </ul>
          </CardContent>
        </Card>
      </main>
    </div>
  )
}
